.vibrant-bg {
  min-height: 100vh;
  background: linear-gradient(120deg, #21034d 0%, #0067ff 55%, #a235d3 100%);
  color: #eafaff;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.dev-header-ext {
  text-align: center;
  padding-top: 32px;
  padding-bottom: 12px;
  background: rgba(19,18,28,0.93);
  border-bottom: 4px solid #b146fa;
  box-shadow: 0 5px 30px #8c39ff1a;
}
.profile-img-hero {
  width: 125px;
  height: 125px;
  object-fit: cover;
  border-radius: 50%;
  border: 4px solid #07e4d3;
  margin-bottom: 12px;
  box-shadow: 0 5px 38px rgba(70,220,255,.2);
  background: #2a004a;
  transition: transform 0.3s;
}
.profile-img-hero:hover {
  transform: scale(1.1) rotate(-7deg);
  box-shadow: 0 0 78px #d900ff65;
}

.dev-hero {
  font-size: 2.52rem;
  font-weight: 700;
  letter-spacing: 2px;
  margin-bottom: 18px;
  color: #4dfac8;
  text-shadow: 0 4px 24px #25049633;
}
.dev-tag { color: #ff52b4; }
.dev-tagname { color: #20ffe4; font-weight: 700; }

.dev-nav {
  margin-top: 6px;
  margin-bottom: 5px;
}
.dev-nav a {
  text-decoration: none;
  color: #20ffe4;
  background: none;
  font-size: 1.14rem;
  font-weight: 600;
  margin: 0 18px;
  transition: color 0.19s, background 0.16s, border-radius 0.14s;
  border-radius: 22px;
  padding: 3px 13px;
  outline: none;
  position: relative;
}
.dev-nav a:hover, .dev-nav a.active {
  color: #ffbc34;
  background: rgba(130,34,255,.16);
  border-radius: 32px;
}

.main-content-box {
  padding: 36px 8vw 18px 8vw;
}

.main-section {
  margin-bottom: 54px;
  background: rgba(56,54,92,0.22);
  border-radius: 18px;
  padding: 28px 5vw 12px 5vw;
  box-shadow: 0 6px 38px #9795e044;
  animation: fadeIn 1.25s;
}
@keyframes fadeIn {
  from { opacity: 0; transform: scale(1.04);}
  to { opacity: 1; transform: scale(1);}
}
.social-row {
  margin-top: 18px;
  display: flex;
  gap: 18px;
  flex-wrap: wrap;
}
.social-btn-vibrant {
  padding: 7px 18px;
  font-size: 1.07rem;
  border-radius: 24px;
  background: linear-gradient(90deg,#2ad9fa,#9b48fa,#ffe735);
  color: #fff;
  box-shadow: 0 3px 16px rgba(60,170,220,0.11);
  text-decoration: none;
  font-weight: 600;
  border: none;
  transition: box-shadow 0.27s, transform 0.21s;
  outline: none;
}
.social-btn-vibrant:hover {
  background: linear-gradient(90deg,#ff52b4,#a249fa);
  box-shadow: 0 0 45px #f600ff80;
  color: #ffe735;
  transform: scale(1.11);
}
.columns-box {
  display: flex;
  gap: 36px;
  flex-wrap: wrap;
}
.skill-container {
  flex: 1 1 175px;
  min-width: 130px;
  margin-bottom: 20px;
  margin-right: 6px;
  padding: 19px 16px 9px 16px;
  background: linear-gradient(86deg, #291a55 53%, #7b4cfa 95%);
  border-radius: 13px;
  box-shadow: 0 1px 15px #7b4cfa45;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.skill-label {
  font-weight: 600;
  color: #ffe735;
  letter-spacing: 1px;
  margin-bottom: 3px;
}
.project-card-vibrant {
  background: linear-gradient(86deg, #241b36 75%, #38bfac 100%);
  border-radius: 14px;
  padding: 21px 18px 17px 18px;
  box-shadow: 0 4px 24px #43dfdd22;
  margin-bottom: 24px;
  min-width: 220px;
  max-width: 310px;
  flex: 1 1 230px;
  transition: box-shadow 0.22s, transform 0.18s, background 0.18s;
  text-align: left;
  color: #ecfeff;
}
.project-card-vibrant:hover {
  background: linear-gradient(96deg, #291a55 53%, #f937ff 95%);
  box-shadow: 0 8px 62px #df43ba44;
  color: #ffe735;
}
.project-icon {
  font-size: 2.12rem;
  margin-bottom: 8px;
  display: block;
}
.project-card-vibrant h3 {
  margin: 0 0 9px 0;
  color: #8bfdc8;
  font-weight: 700;
}
.project-card-vibrant p {
  margin: 0 0 11px 0;
  color: #eafaff;
}
.project-card-vibrant a.project-link {
  color: #8bfdc8;
  text-decoration: none;
  font-weight: 700;
  transition: color 0.17s;
}
.project-card-vibrant a.project-link:hover {
  color: #ffd734;
}
.project-tech {
  margin-top: 8px;
}
.tech-badge {
  display: inline-block;
  background: #29293f;
  color: #ffd734;
  font-weight: 600;
  border-radius: 9px;
  padding: 2px 10px;
  font-size: 0.96rem;
  margin-right: 9px;
  margin-bottom: 5px;
  border: 1px solid #8bfdc8;
}
.contact-grid.contact-vertical {
  flex-direction: column;
  align-items: flex-start;
  gap: 14px;
  margin-top: 20px;
}
.contact-label {
  font-weight: 600;
  margin-right: 7px;
  color: #17ffd7;
  font-size: 1.08rem;
}
.contact-link {
  font-weight: 600;
  color: #ffd700;
  font-size: 1.05rem;
  text-decoration: underline;
  transition: color 0.2s;
}
.contact-link:hover {
  color: #ff52b4;
}

.dev-footer-ext {
  text-align: center;
  font-size: 1rem;
  color: #38ecc8;
  padding: 13px 0 18px 0;
  background: rgba(19,18,28,0.79);
  border-top: 2px solid #b146fa;
}

@media (max-width: 600px) {
  .main-content-box { padding: 18px 1vw; }
  .columns-box { flex-direction: column; }
  .skill-container, .project-card-vibrant { min-width: unset; max-width: 100%; }
  .profile-img-hero { width: 72px; height: 72px; }
  .main-section { padding: 14px 2vw 8px 2vw; }
  .contact-grid.contact-vertical { gap: 11px; }
}
